import $ from 'jquery';
import 'magnific-popup';

const modal = (() => {
	const options = {
		type:'inline',
		closeBtnInside:true,
		removalDelay: 500,
		fixedContentPos : true,
		mainClass: 'mfp-zoom-in'
	};

	$('.link-modal').magnificPopup(options);
})();

export default modal


